<template>
  <div class="g-login-box">
    <van-nav-bar
      title="注册"
      left-arrow
      @click-left="toPageReturn"
    />
    <van-form>
      <van-field
        name="姓名"
        label="姓名"
        placeholder="姓名"
        :rules="[{ required: false, message: '请填写姓名' }]"
      />
      <van-field
        name="联系电话"
        label="联系电话"
        placeholder="联系电话"
        :rules="[{ required: false, message: '请填写联系电话' }]"
      />
      <van-field
        name="验证码"
        label="验证码"
        placeholder="验证码"
        :rules="[{ required: false, message: '请填写验证码' }]"
      />
      <!--<van-field
        name="地址"
        label="地址"
        placeholder="地址"
        v-model="address"
        :rules="[{ required: true, message: '请填写地址' }]"
      />-->
      <van-field
        v-model="state.fieldValue"
        is-link
        readonly
        label="地区"
        placeholder="请选择所在地区"
        @click="state.show = true"
      />
      <van-popup v-model:show="state.show" round position="bottom">
        <van-cascader
          v-model="state.cascaderValue"
          title="请选择所在地区"
          :options="options"
          @close="state.show = false"
          @finish="onFinish"
        />
      </van-popup>
      <van-field
        name="个人图像"
        label="个人图像"
        placeholder="个人图像"
      >
        <template #input>
          <van-uploader />
        </template>
      </van-field>
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit" @click="toPage">
          提交
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { Toast } from 'vant'
import { defineComponent, reactive } from 'vue'
import router from './../router'

export default defineComponent({
  setup () {
    const columns = ['杭州', '宁波', '温州', '绍兴', '湖州', '嘉兴', '金华']
    const state = reactive({
      show: false,
      fieldValue: '',
      cascaderValue: '',
      value: '',
      showPicker: false
    })
    const guide = () => {
      console.log('ddd')
    }
    const onConfirm = (value:string) => {
      state.value = value
      state.showPicker = false
    }
    const options = [
      {
        text: '重庆市',
        value: '330000',
        children: [{
          text: '九龙坡区',
          value: '330100',
          children: [{
            text: '石桥铺',
            value: '330140'
          }]
        }]
      },
      {
        text: '江苏省',
        value: '320000',
        children: [{ text: '南京市', value: '320100' }]
      }
    ]
    // 全部选项选择完毕后，会触发 finish 事件
    const onFinish = ({ selectedOptions }: any) => {
      state.show = false
      state.fieldValue = selectedOptions.map((option: any) => option.text).join('/')
    }
    const onCancel = () => Toast('取消')
    const toPage = () => router.push('/login')
    const toPageReturn = () => router.go(-1)
    return {
      options,
      onFinish,
      guide,
      columns,
      onConfirm,
      onCancel,
      state,
      toPage,
      toPageReturn
    }
  }
})
</script>
