
import { Toast } from 'vant'
import { defineComponent, reactive } from 'vue'
import router from './../router'

export default defineComponent({
  setup () {
    const columns = ['杭州', '宁波', '温州', '绍兴', '湖州', '嘉兴', '金华']
    const state = reactive({
      show: false,
      fieldValue: '',
      cascaderValue: '',
      value: '',
      showPicker: false
    })
    const guide = () => {
      console.log('ddd')
    }
    const onConfirm = (value:string) => {
      state.value = value
      state.showPicker = false
    }
    const options = [
      {
        text: '重庆市',
        value: '330000',
        children: [{
          text: '九龙坡区',
          value: '330100',
          children: [{
            text: '石桥铺',
            value: '330140'
          }]
        }]
      },
      {
        text: '江苏省',
        value: '320000',
        children: [{ text: '南京市', value: '320100' }]
      }
    ]
    // 全部选项选择完毕后，会触发 finish 事件
    const onFinish = ({ selectedOptions }: any) => {
      state.show = false
      state.fieldValue = selectedOptions.map((option: any) => option.text).join('/')
    }
    const onCancel = () => Toast('取消')
    const toPage = () => router.push('/login')
    const toPageReturn = () => router.go(-1)
    return {
      options,
      onFinish,
      guide,
      columns,
      onConfirm,
      onCancel,
      state,
      toPage,
      toPageReturn
    }
  }
})
